.projects {
  padding: 4rem 2rem;
  color: #fff;
  text-align: center;
}

.project-grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  margin-top: 2rem;
}

.project-card {
  background-color: #222;
  padding: 1.5rem;
  border-radius: 8px;
  text-align: left;
  transition: transform 0.2s, background-color 0.2s;
  color: white;
  text-decoration: none;
}

.project-card:hover {
  transform: translateY(-5px);
  background-color: #2a2a2a;
}
